//Lokasi halte bikun
import halteMerah from "../../../data/halteMerah.json"; // 18 halte
import halteBiru from "../../../data/halteBiru.json"; // 19 halte

export const parseIncomingMessage = (message, choosenStop, choosenRoute) => {

  if (choosenStop === "" || choosenRoute === "") return JSON.parse(message);

  let availableBus = null;
  availableBus = JSON.parse(message);

  console.log(choosenStop)
  console.log(choosenRoute)
  console.log(availableBus)

  for (let i = 0; i < availableBus.length; i++) {
    availableBus[i].durations.splice(0, 1);

    if (choosenRoute !== 0 || choosenRoute !== "") {
      if (choosenRoute === 2) {
        availableBus[i].durations.splice(18);
      } else {
        availableBus[i].durations.splice(0, 18);
      }
    }
  }

  if (choosenStop !== "") {
    let halteIndex = null;
    if (choosenRoute !== 0) {
      halteIndex = choosenRoute === 2 ? halteMerah.findIndex(halte => halte.namaHalte === choosenStop) : halteBiru.findIndex(halte => halte.namaHalte === choosenStop);
    } else {
      const bothHalte = Object.assign(halteMerah, halteBiru);
      halteIndex = bothHalte.findIndex(halte => halte.namaHalte === choosenStop);
    }

    for (let i = 0; i < availableBus.length; i++) {
      const eta = availableBus[i].durations.splice(halteIndex, 1);

      availableBus[i].detail = {
        eta: parseETA(eta[0]),
        nextHalte: parseNextHalte(availableBus[i].durations, choosenRoute)
      };

      delete availableBus[i].durations;
    }

    for (let i = 0; i < availableBus.length; i++) {
      if (choosenStop === 2) {
        if (availableBus[i].type === "biru") {

          delete availableBus[i];

        }
      } else if (choosenStop === 1) {
        if (availableBus[i].type === "merah") {

          delete availableBus[i];
        }
      }
    }

    return availableBus;
  }
};


const parseETA = (eta) => {

  let finalETA;
  if (eta < 10) {

    finalETA = 0;

  } else if (eta < 60) {

    finalETA = 1;

  } else {

    finalETA = Math.floor(eta / 60);

  }

  return finalETA;

}

const parseNextHalte = (durations, choosenRoute) => {

  let halteList;
  if (choosenRoute !== 0) {
    halteList = choosenRoute === 2 ? halteMerah : halteBiru;
  } else {
    halteList = Object.assign(halteMerah, halteBiru);
  }

  let nextHalteIndex = 0;

  for (let i = 1; i < durations.length; i++) {
    nextHalteIndex = durations[nextHalteIndex] > durations[i] ? i : nextHalteIndex;
  }

  return halteList[nextHalteIndex].namaHalte;
}


